import React, { useContext } from 'react'
import NavigationMenuItem from './NavigationMenuItem'
import { GlobalContext } from '../App'

export default function NavigationMenu() {

    let globals = useContext(GlobalContext)

    return (
        <ul className={`divide-${globals.accent}-500 text-center divide-y-2 flex flex-col justify-center items-center mt-4 md:mt-2 pb-6`}>
            <NavigationMenuItem text="About" href="#about" />
            <NavigationMenuItem text="Projects" href="#projects" />
            <NavigationMenuItem text="Social" href="#social" />
        </ul>
    )
}
