import React from 'react'

export default function ProjectCard(props) {

    return (
        <a href={props.href} rel="noopener noreferrer" target="_blank" className={` sm:w-full md:w-1/2 bg-white transition ease duration-200 project-${props.id} p-6 rounded-lg my-8 mx-4`}>
            <h1 className="sm:text-2xl md:text-4xl font-optician">{props.name}</h1>
            <img className={`w-screen mt-4 shadow-md hover:shadow-lg transition ease duration-200 rounded-lg`} src={props.img} alt="Project Screenshot"></img>
        </a>
    )
}
