import React, { useContext } from 'react'
import SocialItem from '../Components/SocialItem'
import { GlobalContext } from '../App'
import Title from '../Components/Title'
import { faTwitter } from '@fortawesome/free-brands-svg-icons'
import { faInstagram } from '@fortawesome/free-brands-svg-icons'
import { faDiscord } from '@fortawesome/free-brands-svg-icons'
import { faYoutube } from '@fortawesome/free-brands-svg-icons'
import { faBitcoin } from '@fortawesome/free-brands-svg-icons'

export default function Social() {

    let globals  = useContext(GlobalContext)

    function handleClick(text) {
        navigator.clipboard.writeText(text)
        alert('Copied ' + text + ' to clipboard!')
    }

    return (
        <div id="social" className={`${globals.padding}`}>
            <Title text="Social Media" color={`${globals.accent}-500`}></Title>
            <div className="flex flex-col md:flex-row px-8 pb-16 items-start justify-around">
                <SocialItem icon={faTwitter} color="#1DA1F2" href="https://twitter.com/realnajan" text="@realnajan"></SocialItem>
                <SocialItem icon={faInstagram} color="#C13584" href="https://instagram.com/_najan" text="_najan"></SocialItem>
                <SocialItem icon={faDiscord} color="#7289DA" href="https://discord.gg/YbdAg8D" text="jan#7312"></SocialItem>
                <SocialItem icon={faYoutube} color="red" href="https://www.youtube.com/channel/UCNXdO4QeiSYJ2iBBoEoZkNw" text="Jan"></SocialItem>
                <SocialItem icon={faBitcoin} color="orange" noblank={true} handleClick={handleClick} href="#social" val="3MPPobfztTwmHPntAuYpzus6kEbd3AFi2S" text="Donate"></SocialItem>
            </div>
        </div>
    )
}
