import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export default function SocialItem(props) {

    let target = props.noblank ? "" : "_blank"

    return (
        <a target={target} onClick={() => props.handleClick(props.val)} rel="noopener noreferrer" href={props.href} style={{'color': props.color}} className={`mb-8 text-6xl flex justify-center items-center`}>
            <FontAwesomeIcon icon={props.icon} />
            <span className="text-2xl text-black font-staatliches ml-4">{props.text}</span>
        </a>
    )
}
