import React, { useContext } from 'react'
import Title from '../Components/Title'
import { GlobalContext } from '../App'
import AboutItem from '../Components/AboutItem'
import flag from '../Images/de.png'

export default function About() {

    let globals  = useContext(GlobalContext)

    let img = <img width="135px" className="block about__img" src={flag} alt="German Flag"></img>

    return (
        <div id="about" className={`${globals.padding} pb-16`}>
            <Title text="about" bg="white" color={`${globals.accent}-500`}></Title>
            <div className="flex  flex-col md:flex-row justify-around">
                <AboutItem big="Jan" small="Martchouk"></AboutItem>
                <AboutItem big={img} small="Germany" img={true}></AboutItem>
                <AboutItem big="15" small="Years Old"></AboutItem>
            </div>
        </div>
    )
}
