import React, { useContext } from 'react'
import { GlobalContext } from '../App'
import Title from '../Components/Title'
import ProjectCard from '../Components/ProjectCard'
import img_btc from '../Images/btc.png'
import img_money from '../Images/money.png'

export default function Projects() {

    let globals  = useContext(GlobalContext)

    return (
        <div id="projects" className={`bg-texture ${globals.padding}`}>
            <Title text="projects" color={`${globals.accent}-500`} href="https://bitcoinweek.de" bg="white"></Title>
            <div className="flex flex-col md:flex-row justify-around items-around">
                <ProjectCard img={img_btc} name="Bitcoinweek.de" href="https://bitcoinweek.de" ></ProjectCard>
                <ProjectCard img={img_money} name="Moneypocket" href="https://github.com/realnajan/moneypocket-react/releases"></ProjectCard>
            </div>
        </div>
    )
}
