import React, { useContext } from 'react'
import { GlobalContext } from '../App'

export default function AboutItem(props) {

    let globals = useContext(GlobalContext)

    let classes = props.img ? "mt-6" : "-mt-16"
    let wrapclass = props.img ? "about__image" : ""

    return (
        <div className={`md:w-1/3 font-staatliches flex flex-col items-center justify-center py-16 ${wrapclass}`}>
            <h1 className={`whitespace-no-wrap about-big text-${globals.accent}-500`}>{props.big}</h1>
            <p className={`whitespace-no-wrap text-black text-2xl px-4 py-2 ${classes}`}>{props.small}</p>
        </div>
    )
}
