import React, { useState } from 'react';
import Navigation from './Components/Navigation'
import Header from './Views/Header'
import About from './Views/About'
import Projects from './Views/Projects'
import Social from './Views/Social';
import ColorSelector from './Components/ColorSelector'
import createPersistedState from 'use-persisted-state';
const useGlobalsState = createPersistedState('globals');

export const GlobalContext = React.createContext({})

function App() {

  const [ globals , setGlobals] = useGlobalsState({
    "padding": "xl:px-64 lg:px-24 pd:px-16 px-2",
    "accent": "indigo"
  })

  return (
    <GlobalContext.Provider value={globals}>
    <div className="App h-screen w-screen overflow-x-hidden">
      <Header/>
      <About />
      <Projects />
      <Social />
    </div>
    <ColorSelector setGlobals={setGlobals}/>
    </GlobalContext.Provider>
  );
}

export default App;
