import React, { useContext } from 'react'
import { GlobalContext } from '../App'

export default function ColorSelector(props) {

    let globals = useContext(GlobalContext)

    let colors = ['red', 'orange', 'yellow', 'green', 'teal', 'blue', 'indigo', 'purple', 'pink']

    function handleClick(color) {
        let newGlobals = {}
        newGlobals.accent = color
        props.setGlobals({...globals, ...newGlobals})
        console.log(newGlobals)
    }

    let divs = colors.map(color => {
        if(globals.accent === color) {
            return <div className={`transition ease-in-out duration-300 mb-3 cursor-pointer w-6 rounded-full h-6 inline-block border-4 border-${color}-600 bg-${color}-500`}></div>
        } else {
            return <div onClick={() => handleClick(color)} className={`transition ease-in-out duration-300 mb-3 cursor-pointer w-6 rounded-full h-6 inline-block border-4 border-${color}-500 bg-${color}-500`}></div>
        }
    })

    return (
        <div className={`z-50 shadow-lg rounded-r-lg bg-white p-2 fixed margin-auto centered flex flex-col`}>
            {divs}
        </div>
    )
}
