import React, { useContext } from 'react'
import pic from '../Images/pic.png'
import { GlobalContext } from '../App'
import NavigationMenu from '../Components/NavigationMenu'
import { ReactSVG } from 'react-svg'
import pattern from '../Images/pattern.svg'
import logo from '../Images/jan.svg'


export default function Header() {

    let globals  = useContext(GlobalContext)

    return (
        <div className={`text-${globals.accent}-500 overflow-hidden relative ${globals.padding}`}>
            <div className="flex flex-col md:flex-row justify-center items-center w-full headerwrap md:h-screen">
            <div className={`md:mr-16 text-${globals.accent}-500`}>
                <ReactSVG beforeInjection={(svg) => {
                    svg.classList.add('header__logo')
                    svg.classList.add('fill-current')
                }} src={logo}></ReactSVG></div>
                <NavigationMenu></NavigationMenu>              
            </div>
            <ReactSVG beforeInjection={(svg) => 
                {svg.classList.add('bgpattern')
                svg.classList.add('fill-current')

            }} src={pattern}></ReactSVG>
        </div>
    )
}
